<template>
    <div class="container">
        <div class="card">{{word}}</div>
        <div class="btn-group">
            <button class='btn left btn-danger' @click='forget'>x</button>
            <button class='btn right btn-success' @click='remember'>√</button>
        </div>
    </div>
</template>

<script>
import Data from '../data';

export default {
    data () {
        return {
            ind: [0, 0],
        }
    },
    computed: {
        word () {
            return Data['hiragana'][this.ind[0]][this.ind[1]];
        }
    },
    methods: {
        generateInd () {
            this.ind = [this.getRandom(10), this.getRandom(5)];
        },
        getRandom (max, min = 0) {
            return Math.floor(Math.random() * (max - min));
        },
        forget () {
            this.generateInd();
        },
        remember () {
            this.generateInd();
        }
    },
    created () {
        this.generateInd(); 
    }
}
</script>

<style scoped>
.container {
    width: 50%;
    margin: 0 auto;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.card{
    width: 100%;
    height: 50vw;
    line-height: 50vw;
    border: 2px dashed #999;
    font-size: 7rem;
    text-align: center;
    background: #fff;
}
.btn-group{
    margin-top: 8px;
    overflow: hidden;
}
.btn{
    width: 30px;
    height: 30px;
    line-height: 30px;
    background: #fff;
    border: 1px solid #aaa;
    box-sizing: content-box;
    border-radius: 3px;
    font-size: 1rem;
}
.btn-danger{
  background: red;
  border-color: darkred;
  color: #fff;
}
.btn-success{
    background: lightgreen;
    border-color: green;
    color: darkgreen;
}
</style>





